import React, { useState } from "react";
import people from "./data";
import { FaChevronLeft, FaChevronRight, FaQuoteRight } from "react-icons/fa";

const Review = () => {
  const [index, setIndex] = useState(0);

  function randomIndex() {
    return Math.floor(Math.random() * people.length);
  }
  console.log(randomIndex());
  const { id, name, image, job, text } = people[index];
  console.log(index, id);

  return (
    <section className="container">
      <div className="title">
        <h2>Our Reviews</h2>
        <div className="underline"></div>
      </div>
      <article key={id} className="review">
        <div className="img-container">
          <img className="person-img" src={image} alt={image} />
          <span className="quote-icon">
            <FaQuoteRight />
          </span>
        </div>
        <h4 className="author">{name}</h4>
        <p className="job">{job}</p>
        <p className="info">{text}</p>
        <div className="button-container">
          <button
            className="prev-btn"
            onClick={() => setIndex(index > 0 ? index - 1 : people.length - 1)}
          >
            <FaChevronLeft />
          </button>
          <button
            className="next-btn"
            onClick={() => setIndex(index < people.length - 1 ? index + 1 : 0)}
          >
            <FaChevronRight />
          </button>
        </div>
        <button className="random-btn" onClick={() => setIndex(randomIndex())}>
          Surprise Me
        </button>
      </article>
    </section>
  );
};

export default Review;
